.room__statistics {
  position: relative;
  float: left;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  top: -3px;
  p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 60;
    color: $purple;
    font-size: 24px;
    font-weight: 700;
    span {
      padding-top: 4px;
      font-size: 12px;
      text-transform: uppercase;
    }
  }
}

.room__statistics::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 50;
  width: 93%;
  height: 93%;
  color: $purple;
  background-color: #fff;
  border-radius: 50%;
}

.room__statistic {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #aaaaaa;
  border-radius: 50%;
  clip: rect(0px, 60px, 150px, 0px);
}

#great {
  z-index: 10;
  background: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
  transform: rotate(0deg);
}

#well {
  z-index: 20;
  background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
  transform: rotate(180deg);
  clip: rect(0px, 58px, 59px, 0px);
}

#satisfactory {
  z-index: 30;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  transform: rotate(90deg);
  clip: rect(0px, 59px, 58px, 0px);
}

#disappointed {
  z-index: -1;
  background: transparent;
  transform: rotate(0deg);
}

.room__stats {
  float: left;
  margin-left: 45px;
}
.room__stats-list {
  margin-top: 27px;
}
.room__stats-item {
  position: relative;
  margin: 9px 0;
  color: $dark75;
}

.room__stats-item::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -15px;
  width: 10px;
  height: 10px;
  background: linear-gradient(180deg, #919191 0%, #3d4975 100%);
  border-radius: 100%;
}

.room__stats-item:first-child::before {
  background: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
}

.room__stats-item:nth-child(2)::before {
  background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
}

.room__stats-item:nth-child(3)::before {
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}
@media (max-width: 665px) {
  .room__stats {
    margin-left: 30px;
  }
}
@media (max-width: 540px) {
  .room__stats {
    margin-left: 22px;
  }
}
